import { useState, useRef } from 'react';
import PropTypes from 'prop-types';
import BasePage from 'pages/base_page';
import Authenticate from 'hocs/require-authentication';
import ToggleViewMode from 'components/memory/ToggleViewMode';
import ModeContent from 'components/memory/ModeContent';

export default function CreateMemory({ bucket, authURL }) {
  const modes = ['Create', 'Preview'];
  const [mode, setMode] = useState(modes[0]);
  const ref = useRef(null);

  return (
    <BasePage>
      <Authenticate>
        <ToggleViewMode mode={mode} handleModeChange={setMode} modes={modes} />
        <ModeContent mode={mode} formRef={ref} bucket={bucket} authURL={authURL} />
      </Authenticate>
    </BasePage>
  );
}

CreateMemory.propTypes = {
  bucket: PropTypes.string.isRequired,
  authURL: PropTypes.string.isRequired,
};
