import Grid from '@mui/material/Grid';

import Thumbnail from 'pages/Thumbnail';
import MemoryContent from 'pages/memory/MemoryContent';
import PreviewCardHeader from './PreviewCardHeader';

export default function PreviewMemory(title, date, imageDetails, thumbnail, story) {
  return (
    <Grid container justifyContent="space-evenly" sx={{ p: 0 }}>
      <Grid item xs={6} sm={4} md={4} lg={3} xl={2} sx={{ px: 1, mb: 1 }}>
        <PreviewCardHeader title="Thumbnail Preview" />
        <Thumbnail
          title={title}
          date={date || ''}
          thumbnailImage={imageDetails.length > 0 ? imageDetails[thumbnail].url : ''}
          handleClick={() => {}}
        />
      </Grid>

      <Grid item xs={12} lg={9} xl={10}>
        <PreviewCardHeader title="Memory Preview" />
        <MemoryContent
          memType="preview"
          memTitle={title}
          memDate={date}
          memStory={story}
          memImages={imageDetails.map((image) => ({
            src: image.url,
            width: image.width,
            height: image.height,
            caption: image.caption || '',
          }))}
          imageNames={imageDetails.map((image) => image.name)}
        />
      </Grid>
    </Grid>
  );
}
